:root{
  --mainBackgroundcolor : #eeeeee;
  --mainWidgetcolor : #d9d9d9;
  --mainFontcolor : #373737;
}

html, body{
  margin: 0;
  padding : 0;
  background-color: var(--mainBackgroundcolor);
  color : var(--mainFontcolor);
  position: relative;
}

.App{
  height: 100vh;
}

.Top-Menu-Fix {
  width: 100%;
  height: auto;
  position: sticky;
  top : 0;
  z-index: 999;
}