.Profile{
    /* font-family: ; */
}

.Profile_PC-banner{
    padding: 30px 30px 0 30px;
    height: 300px;
}

.Profile_PC-banner-tabs-menu{
    padding-right: 30px;
}

.Profile_PC-banner-tabs-menu{
    border-bottom-width: 2px;
    border-bottom-color: black;
    font-size: 18px;
}

.Profile_PC-banner-tabs-menu > li{
    width: 150px;
    height: 50px;
}

.Profile_PC-banner-tabs-menu > li.Choosed{
    background-color: white;
    border: 2px solid black;
    border-bottom : none;
    position: relative;
}

.Profile_PC-banner-tabs-menu > li.Choosed::after{
    content : "";
    position: absolute;
    top:100%;
    left:0; 
    width : 100%;
    height: 2px;
    background-color: white;
}