.Contact{
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
    font-family : 'ONE-Mobile-Title';
}

/* pc */
.Contact_PC{
    padding: 0 100px;
}

.Contact_PC-left > div{
    width: 400px;
    font-size: 32px;
    font-weight: bold;
    padding-left: 26px;
}

.Contact_PC-left > div:first-child{
    font-size: 72px;
    padding-left: 0px;
}

.Contact_PC-right > div{
    width: 400px;
    font-size: 32px;
    font-weight: bold;
}

.Contact_PC-right_Sns-ball{
    padding-top : 20px;
    padding-left: 20px;
    width: 420px;
}

.Contact_PC-right_Sns-balls{
    width: 80px;
    height: 80px;
    border-radius: 40px;
    background-color: var(--mainWidgetcolor);
    font-size: 18px;
}

/* mb */
.Contact_MB{
    padding: 0 30px;
}

.Contact_MB-top > div{
    font-size: 28px;
    font-weight: bold;
    padding-left: 16px;
}

.Contact_MB-top > div:first-child{
    font-size: 64px;
    padding-left: 0px;
}

.Contact_MB-bottom > div{
    font-size: 32px;
    font-weight: bold;
}

.Contact_MB-bottom_Sns-ball{
    padding-top : 20px;
    padding-left: 6px;
}

.Contact_MB-bottom_Sns-balls{
    width: 70px;
    height: 70px;
    border-radius: 100%;
    background-color: var(--mainWidgetcolor);
    font-size: 18px;
}

@media only screen and (max-width: 900px) {
    .Contact_PC{
        display: none ;
    }
    .Contact_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Contact_PC{
        display: flex ;
    }
    .Contact_MB {
        display: none;
    }
}
  