.Home{
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
    bottom: 0;
    font-family : 'ONE-Mobile-Title';
}

/* pc */
.Home_PC-left, .Home_PC-right{
    height: 400px;
}

.Home_PC-left > div:first-child {
    width: 380px;
    height: 380px;
    border-radius: 200px;
    background-color: var(--mainWidgetcolor);
}

.Home_PC-left img {
    width: 280px;
}

.Home_PC-right > div{
    font-size: 24px;
    font-weight: bold;
    padding-left: 20px;
}

.Home_PC-right > div:first-child{
    font-size: 64px;
    padding-left: 0px;
    padding-bottom: 6px;
}

.Home_PC-right_Sns-ball{
    padding-top : 24px;
    padding-left: 40px;
    width: 420px;
}

.Home_PC-right_Sns-balls{
    width: 80px;
    height: 80px;
    border-radius: 40px;
    background-color: var(--mainWidgetcolor);
    font-size: 24px;
}

/* mb */
.Home_MB{
    padding: 0 30px;
}

.Home_MB-top > div{
    background-color: var(--mainWidgetcolor);
    width: 320px;
    height: 320px;
    border-radius: 100%;
}

.Home_MB-top img{
    width: 220px;
}

.Home_MB-bottom > div{
    font-size: 18px;
    font-weight: bold;
    padding-left: 6px;
}

.Home_MB-bottom > div:first-child{
    font-size: 42px;
    padding-left: 0px;
    padding-bottom: 2px;
}

.Home_MB-bottom_Sns-balls{
    width: 70px;
    height: 70px;
    border-radius: 100%;
    background-color: var(--mainWidgetcolor);
    font-size: 18px;
}

@media only screen and (max-width: 900px) {
    .Home_PC{
        display: none ;
    }
    .Home_MB {
        display: flex;
    }
}

@media only screen and (min-width: 901px) {
    .Home_PC{
        display: flex ;
    }
    .Home_MB {
        display: none;
    }
}
  